.tile {
	display: grid;
	place-content: center;
	width: 100px;
	height: 100px;
}

.tile .chess-piece {
	width: 100px;
	height: 100px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100px;
	z-index: 1000;
}

.tile .chess-piece:hover {
	cursor: grab;
}

.tile .chess-piece:active {
	cursor: grabbing;
}

.black-tile {
	background-color: rgb(172, 138, 102);
}

.white-tile {
	background-color: rgb(235, 217, 183);
}

.tile img {
	width: 80px;
}
